<template>
  <div>
    <div class="remoteTuning">
      <el-row type="flex" :gutter="10" class="query-container">
        <el-col :span="5">
          <el-date-picker
            v-model="query.dateRange"
            type="daterange"
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            placement="bottom-start"
          >
          </el-date-picker>
        </el-col>
        <el-col :span="4">
          <el-input v-model="query.name" placeholder="请输入发电机组名称"> </el-input>
        </el-col>
        <el-col :span="3"
          ><el-select v-model="query.state1">
            <el-option v-for="(e, i) in option1" :key="i" :label="e.label" :value="e.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="3"
          ><el-select v-model="query.state2">
            <el-option v-for="(e, i) in option2" :key="i" :label="e.label" :value="e.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4"
          ><el-input placeholder="请输入客户姓名" v-model="query.nameCustomer"></el-input>
        </el-col>
        <el-col :span="2"
          ><el-button @click="handleQueryBtn" type="primary">搜索</el-button>
        </el-col>
      </el-row>
      <!-- <el-row class="table-container"> -->
      <!-- <Table
          @handleOperationBtn="handleOperationBtn"
          :columnDataOperation="columnDataOperation"
          :tableData="tableData"
          :columnData="columnData"
        ></Table> -->
      <table-list
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        :dataTotal="dataTotal"
        :pagination="paginationCfg"
        @handleChangePage="handleChangePage"
        @handleChangeSize="handleChangeSize"
        border
      >
        <el-table-column
          slot="operation"
          label="操作"
          align="center"
          v-bind="options && options.props"
        >
          <template slot-scope="scope">
            <div class="operate-group">
              <el-button
                v-if="!!scope.row.IsCanConfirm && scope.row.Status.toString() === '待确认'"
                type="text"
                @click="handleCheck1(scope.row)"
              >
                确认</el-button
              >
              <el-button
                v-if="!!scope.row.IsCanConfirmAndEdit && scope.row.Status.toString() === '待确认'"
                type="text"
                @click="handleCheck2(scope.row)"
              >
                确认
              </el-button>
              <el-button
                v-if="scope.row.IsConfirmPerson && scope.row.Status.toString() === '已确认'"
                type="text"
                @click="handleOperationBtn(scope.row, 'Operation')"
              >
                调参
              </el-button>
              <el-button
                type="text"
                v-if="scope.row.Status.toString() === '已完成'"
                @click="handleOperationBtn(scope.row, 'History')"
              >
                历史
              </el-button>
              <el-button
                v-if="scope.row.IsCanConfirm || scope.row.IsCanConfirmAndEdit"
                type="text"
                @click="handleDelete(scope.row, 'Operation')"
              >
                删除
              </el-button>
              <!-- <el-button type="text" @click="handleDelete(scope.row)"> 删除 </el-button> -->
            </div>
          </template>
        </el-table-column>
      </table-list>
      <!-- </el-row> -->
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs'

// import _data from './_data.js'
import { isArray } from '@/utils/validate.js'
import { formatTimeDayjs } from '@/utils/common'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'

export default {
  name: 'RemoteTuning',
  mixins: [paginationConfigMixin],
  data() {
    return {
      query: {
        // 默认今日开始倒数7天
        // TODO 测试
        dateRange: [dayjs().add(-700, 'd'), dayjs()],
        // dateRange: [dayjs().add(-7, 'd'), dayjs()],
        name: '',
        state1: '-1',
        state2: '-1',
        nameCustomer: ''
      },
      option1: [
        { value: '-1', label: '全部' },
        { value: '0', label: '待确认' },
        { value: '1', label: '已确认' },
        { value: '2', label: '已完成' },
        { value: '3', label: '已评价' }
      ],
      option1Mapping: {
        0: '待确认',
        1: '已确认',
        2: '已完成',
        3: '已评价'
      },
      option2: [
        { value: '-1', label: '全部' },
        { value: '0', label: '无人值守' },
        { value: '1', label: '有人值守' }
      ],

      dataSource: [],
      options: {
        height: '100%',
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 126 // 操作列宽度
        }
      },
      columns: [
        {
          prop: 'No',
          label: '服务单编号',
          width: 100
        },
        {
          prop: 'Create_Name',
          label: '申请人',
          width: 100
        },
        {
          prop: 'Create_Time',
          label: '申请时间',
          width: 100
        },
        {
          prop: 'GeneratorName',
          label: '机组名称',
          width: 100
        },
        {
          prop: 'ClientName',
          label: '客户姓名',
          width: 100
        },
        {
          prop: 'ClientPhone',
          label: '客户联系方式',
          width: 100
        },
        {
          prop: 'Status',
          label: '状态',
          width: 100
        },
        {
          prop: 'IsConfirmPerson',
          label: '是否可调参',
          isShow: (_) => false
        },
        {
          prop: 'GeneratorSetId',
          label: '机组编码',
          isShow: (_) => false
        }
      ]
    }
  },
  props: [''],
  //   inject: [''],
  //   provide() {
  //     return {}
  //   },
  computed: {},
  watch: {},
  methods: {
    handleCheck1(e) {
      this.$confirm('是否确认？')
        .then((value) => {
          const p = {},
            q = { id: e.Id, code: '' }
          this.$api.operation.validateAuthCode(p, q).then((value) => {
            if (value.Code === 1) {
              this.$message(value.Message || '成功！')
              this.getCurrentPageData()
            }
          })
        })
        .catch((err) => {})
    },
    handleCheck2(e) {
      this.$confirm('是否确认？')
        .then((value) => {
          const p = {
              ClientCompany: e.ClientCompany,
              ClientName: e.ClientName,
              ClientPhone: e.ClientPhone
            },
            q = { id: e.Id }
          this.$api.operation.validateAuthCode(p, q).then((value) => {
            if (value.Code === 1) {
              this.$message(value.Message || '成功！')
              this.getCurrentPageData()
            }
          })
        })
        .catch((err) => {})
    },
    handleDelete(e) {
      this.$confirm('是否确认删除？')
        .then((value) => {
          const p = {},
            q = { id: e.Id }
          this.$api.operation.deleteGenSetServiceSheet(p, q).then((value) => {
            if (value.Code === 1) {
              this.$message(value.Message || '成功！')
              this.getCurrentPageData()
            }
          })
        })
        .catch((err) => {})
    },
    // 分页列表组件所需方法
    queryTableData() {
      this.getCurrentPageData()
    },
    // 列表操作按钮事件处理
    handleOperationBtn(row, name) {
      // this.$router.push({
      //   path: path,
      //   query: {
      //     No: row.No,
      //     GeneratorSetId: row.GeneratorSetId
      //   }
      // })
      this.$emit('changeIs', name, {
        No: row.Id,
        GeneratorSetId: row.GeneratorSetId,
        SheetId: row.Id
      })
    },
    // 搜索事件处理
    handleQueryBtn() {
      // [x] 当当前页码数大于搜索数据总页码数时,无法获取数据,此处需初始化页码为1
      this.paginationCfg.currentPage = 1
      this.getCurrentPageData()
    },
    // 获取本页列表数据
    getCurrentPageData() {
      const query = this.query
      const q = {
        page: this.paginationCfg.currentPage,
        pageSize: this.paginationCfg.pageSize
      }
      const p = {
        StartTime: dayjs(query.dateRange[0]).format('YYYY-MM-DD'),
        EndTime: dayjs(query.dateRange[1]).format('YYYY-MM-DD'),
        GeneratorName: query.name,
        Status: query.state1,
        Type: query.state2
      }
      this.$api.operation.getGenSetServiceSheetList(p, q).then((value) => {
        // value = _data
        const dataList = value.Data && value.Data.Data && value.Data.Data
        const TotalItemCount = value.Data && value.Data.TotalItemCount
        const dataSource = new Array()
        if (dataList && isArray(dataList)) {
          dataList.forEach((e) => {
            const obj = {
              ...e
            }
            obj.Create_Time = formatTimeDayjs(e.Create_Time).format('YYYY-MM-DD HH:mm')
            obj.Status = this.option1Mapping[e.Status]
            dataSource.push(obj)
          })
        }
        this.dataTotal = TotalItemCount
        this.dataSource = dataSource
      })
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    this.getCurrentPageData()
  },
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>
<style lang='less' scoped>
// 使日期间隔选择器的宽度自适应
/deep/ .el-date-editor {
  width: auto;
}
/deep/ .el-select {
  width: 100%;
}
/deep/ .remoteTuning {
  @heightTop: 60px;
  height: 100%;
  .query-container {
    height: @heightTop;
  }
  .table-form-container_content {
    height: calc(100% - @heightTop);
  }
  .table-container {
    height: calc(100% - @heightTop);
    // overflow: scroll;
  }
}
</style>


