<template>
  <div>
    <BMainTopComponent :isShowBackBtn="false" :isShowRefreshBtn="false" />
    <!-- 不要缓存子页面 -->
    <keep-alive include="RemoteTuning">
      <component
        v-if="currentComponentNew === 'MainPage'"
        @changeIs="changeIs"
        :myProp="myPropData"
        :is="currentComponent[0]"
      ></component>
      <component
        v-if="currentComponentNew === 'History'"
        @changeIs="changeIs"
        :myProp="myPropData"
        :is="currentComponent[1]"
      ></component>
      <component
        v-if="currentComponentNew === 'Operation'"
        @changeIs="changeIs"
        :myProp="myPropData"
        :is="currentComponent[2]"
      ></component>
    </keep-alive>
  </div>
</template>

<script>
import MainPage from './mainPage.vue'
import History from './history/index.vue'
import Operation from './operation/index.vue'

export default {
  name: 'RemoteTuning',
  components: {
    MainPage,
    History,
    Operation
  },
  data() {
    return {
      currentComponent: ['MainPage', 'History', 'Operation'],
      currentComponentNew: 'MainPage',
      myPropData: {}
    }
  },
  props: [''],
  //   inject: [''],
  //   provide() {
  //     return {}
  //   },
  computed: {},
  watch: {},
  methods: {
    changeIs(c, prop) {
      const propTemp = { ...prop }
      this.currentComponentNew = c
      this.myPropData = propTemp
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>


